<template>
  <div>
    <img :src="lighteru" v-if="show" />
    <div v-if="show" class="tag close" @click="close"></div>
  </div>
</template>
<script>
import lighteru from '../assets/images/lighter.gif'

export default {
  name: 'Lighteru',
  data: function() {
    return {
      lighteru: lighteru,
      show: false,
    }
  },
  methods: {
    showLighter: function() {
      this.$emit('found');
      this.show = true
    },
    close: function() {
      this.show = false
    },
  },
}
</script>
<style scoped>
img {
